import * as React from 'react';
import { ContentPage } from '../../scripts/components';

      import { PageContent, Markdown } from '../../scripts/components';

      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/general/02-Upcoming.md";
      const html = `
<h1 id="upcoming-features">Upcoming Features</h1>
<p>The GitHub project management features are definitely nice, but they do not provide enough space for discussion and an in-depth presentation of the upcoming features. This list is not ordered in any way (importance, time-to-release, ...) and should only be regarded as a collection of ideas and reminders.</p>
<h2 id="cssom-view-module">CSSOM View Module</h2>
<p>Rendering might be one of the most interesting aspects of AngleSharp. AngleSharp won't cover rendering directly. Instead, it will be easy to hook up a custom rendering engine. A (sample) library will probably be provided, like with the AngleSharp.Scripting one, which serves as an example for including a JavaScript engine. One of the cornerstones of rendering is including the CSSOM view module, which specifies a lot of methods, interfaces and more. This will be crucial when connecting to a real device, such as a screen. Also fake devices, which might be used for analysis (see <code>Window</code>), profit from the CSSOM view module.</p>
<p>Not all interfaces and methods have to be implemented / provided by the core AngleSharp library. Instead one of the main tasks in this work package is to determine a good hierachy, which provides both, flexibility and information, which can be used for many purposes.</p>
<p><em>Status</em>: Partially included. Most of these things will be determined once rendering (ext. library) is scratched.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://dev.w3.org/csswg/cssom-view/">W3C CSSOM</a></li>
</ul>
<h2 id="rendering">Rendering</h2>
<p>Okay, so here it is. There will be rendering, however, not directly within AngleSharp. However, to see what is (still) definitely required from a core perspective, a reference implementation has to be provided. This implementation should follow the official standard described in the CSS 2.1 specification and modules that arrived later.</p>
<p><em>Status</em>: Sketched, outlined, but nothing happened so far.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://www.w3.org/TR/CSS21/">W3C CSS 2.1 specification</a></li>
<li><a href="https://github.com/ArthurHub/HTML-Renderer">HTML Renderer project</a></li>
<li><a href="https://developer.mozilla.org/en-US/docs/Web/Guide/CSS/Visual_formatting_model">MDN Visual Formatting</a></li>
</ul>
<h2 id="define-ui-handling">Define UI Handling</h2>
<p>Most of this section is an extension to <code>IWindow</code>. We require a few points:</p>
<ul>
<li>UI specific tree traversal, keeping track of styling</li>
<li>The event loop (right now placed in <code>Document</code>)</li>
<li>...</li>
</ul>
<p>Most of these things will be more obvious once a sample renderer implementation will be done and released. This will happen after v0.7, probably in October or more likely in November.</p>
<p>The core source should be a browsing context. This, however, is a point on its own. A browsing context will be superior to <code>IWindow</code> instances, which are tightly bound to <code>IDocument</code> instances. A browsing context will also carry the device information, which is currently available at the <code>IWindow</code> level.</p>
<p><em>Status</em>: Completely missing.</p>
<h2 id="finish-style-resolution">Finish Style Resolution</h2>
<p>On a simple level the style resolution is already finished. However, with pseudo elements still missing, the <code>IWindow</code> being unfinished and other open questions, it is not possible to consider style resolution being solved.</p>
<p>In v0.7 there won't be any change (besides bugfixes and some minor feature updates) to the current status. First the CSS object model has to be refined / finished. Once the generated CSS tree is satisfying the style resolution will be on the list.</p>
<p>As a fact the style resolution is really important for (any) rendering project. Having style information is the most important thing for drawing the visual tree. In the end the visual tree is mostly based on the style information. The general rule is: Style + Text = presentation.</p>
<p><em>Status</em>: Will be finished after the CSSOM is finished. This will likely happen for v1.0.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://www.w3.org/TR/DOM-Level-2-Style/css.html#CSS-CSSview-getComputedStyle">W3C DOM2 View</a></li>
<li><a href="http://dev.w3.org/csswg/cssom/#dom-window-getcomputedstyle">W3C CSSOM getComputedStyle</a></li>
<li><a href="http://dev.w3.org/csswg/cssom/#resolved-values">W3C CSSOM resolved values</a></li>
</ul>
<h2 id="provide-further-extension-points">Provide Further Extension Points</h2>
<p>For instance an automatic file system buffer, which (obviously) requires access to the file system. Additionally storage might require access to the file system, but maybe in a different form. Also media types such as video, audio or image will require access to video and audio streaming / playing, as well as image displaying.</p>
<p>It is highly likely that v0.7 will already introduce some media extensions, such as gathering image or audio information (from raw bytes), or accessing the storage for the offline API. This will also introduce the extended configuration pipeline.</p>
<p>The extended configuration pipeline will be the core during DOM interaction. While the parser is the core during source parsing, the configuration pipeline contains all related data.</p>
<p>Already available are, e.g.:</p>
<ul>
<li>Style parsers</li>
<li>Scripting engines</li>
<li>HTTP requester</li>
</ul>
<p>Missing among others are:</p>
<ul>
<li>Mime to media type</li>
<li>Resource database</li>
<li>Local storage</li>
</ul>
<p>The ultimate goal is to have a multitude of extension points, which can then be fully customized, extended and used. This way AngleSharp stays quite atomic (parsers, DOM) and provides the abilities to grow (beyond local). A headless browser that is fully customizable and extensible.</p>
<p><em>Status</em>: Available, more ideas needed.</p>
<h2 id="possibility-of-(simple%3F)-xpath-parsing">Possibility of (Simple?) XPath Parsing</h2>
<p>Right now CSS selectors is the way of querying the document. Nevertheless, there are people who are quite dissatisfied with this solution. Most of those people are not web developers, and are familiar with even more powerful queries in the form of XPath. Unfortunately XPath is currently not support on a standard level.</p>
<p>It must be evaluated how much effort / code a XPath query engine (simple is enough, but it is has to be standard conform) requires. If it could be implemented with limited time efforts, then it should be included. Otherwise the possibility of including other / arbitrary query engines must be evaluated. In general this could be a very interesting path.</p>
<p><em>Status</em>: Completely missing from core, some (external?) library such as <code>AngleSharp.XPath</code> makes sense.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://www.w3.org/TR/DOM-Level-3-XPath/xpath.html#XPathEvaluator">W3C XPathEvaluator IDL</a></li>
<li><a href="http://www.w3.org/TR/xpath/">W3C XPath Specification</a></li>
</ul>
<h2 id="mathml">MathML</h2>
<p>MathML is a markup language that should allow writing equations like LaTeX. Therefore MathML has special tags, which carry a semantic meaning, such as being an operator, an identifier, a symbol, space, raw text and much more. HTML5 allows embedding of MathML by specification.</p>
<p>Right now AngleSharp can handle MathML as foreign elements in HTML. This is the way it should be. There are no plans to support &quot;Math documents&quot; or similar constructs. However, right now AngleSharp is limited to only a few MathML elements. All other elements will be regarded as a general <code>MathElement</code> element, with a custom <code>NodeName</code>.</p>
<p>There are several reasons to include more information on the MathML (sub) DOM. Therefore it is crucial to include all other specifically defined elements in AngleSharp. In the end the <code>MathFactory</code> has to be extended to also support these other elements. That should be sufficient to support MathML.</p>
<p>It has to be investigated if other properties, such as special entities, need to be included as well. If so, the question is: How to change the entity retrieval mode? There are several options, but the solution has to be robust, easy to extend, and well-performing.</p>
<p><em>Status</em>: Basics included, shifted back to v1.0.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://www.w3.org/TR/MathML3/">W3C MathML 3.0 TR</a></li>
</ul>
<h2 id="svg">SVG</h2>
<p>SVG is an XML based vector file format. HTML5 allows embedding SVG images in the document by specification. Some older browsers may require SVG images to be referenced (externally). AngleSharp supports HTML5 and therefore allows SVG to be included directly in the document.</p>
<p>Even though there are no plans for a &quot;Math document&quot;, AngleSharp should consider having an &quot;SVG document&quot; in form of using the <code>IXmlDocument</code> interface (with the <code>XMLDocument</code> implementation). That way external SVG images can be parsed as well. It is yet unclear, if such documents will just use the HTML parser, or if the XML parser will be revived for this. If so it will be probably downgraded, to drop support for DTD and others. That way, the XML parser will be non-validating. This is probably the best solution.</p>
<p>There are lots of special elements and attributes, which need to be covered. In the end, however, this effort will be much less than implementing the HTML DOM.</p>
<p><em>Status</em>: Basics included, shifted back to v1.0.</p>
<p>More information can be found at:</p>
<ul>
<li><a href="http://www.w3.org/TR/SVG11/">W3C SVG 1.1 TR</a></li>
</ul>
<h2 id="slimming-anglesharp">Slimming AngleSharp</h2>
<p>AngleSharp is not really giant, but it is continuously growing. While the project will never drop its core consisting of</p>
<ul>
<li>HTML parser,</li>
<li>CSS parser,</li>
<li>(Core) IDL interfaces,</li>
<li>(Core) DOM implementation,</li>
<li>configuration and extensions,</li>
</ul>
<p>some of the contained abilities should be moved to other (extension) projects. Beginning with the completion of v0.8 a major evaluation will begin, to determine which parts are core / important and which are extensions. The main purpose of this evaluation will be:</p>
<ul>
<li>The keep AngleSharp small for small tasks</li>
<li>To reduce the footprint</li>
<li>Making AngleSharp even more extensible</li>
<li>Providing extensions only if they are required</li>
</ul>
<p>So if one includes AngleSharp just to parse a few lines of HTML, or a single HTML document, this task should be possible only with the core library. If one wants to make screenshots of webpages, emulate a full browsing context, or integrate JavaScript fully with AngleSharp (not just on top of a document), then the extensions are here to help.</p>
<p>In the end the idea is to give users only what they need, not more. If they want more, they can opt-in by getting more advanced NuGet packages, which work as extensions for the core library.</p>
<p><em>Status</em>: CSS already moved out. More to come until v1.0.</p>
`;
    

export default () => (
  <ContentPage>
    
      <PageContent meta={{"title":"Upcoming Features"}}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    
  </ContentPage>
);